import {useState} from "react";
import {Link} from "react-router-dom";
import {ref, uploadBytes, getDownloadURL} from "firebase/storage";
import {storage} from "../firebaseConfig";
import {updateProfile} from "firebase/auth";
import userImg from "../style/images/user.png";
import "../style/Edit.css";

const Edit = ({user, avatarURL ,setAvatarURL}) => {
    const [avatarFile, setAvatarFile] = useState(null);

    return (
        <div id="editCon">
            <div id="editCon0">Edit Profile</div>
            <div id="editCon1">
                <div id="editCon1Child0">
                    <img className="editCon1Child0" src={userImg}/>
                </div>
                <div id="editCon1Child1">
                    <input className="editCon1Child1" type="file" onChange={(e) => {
                        setAvatarFile(e.target.files[0]);
                    }}/>
                </div>
                <div id="editCon1Child2">
                    <Link className="editCon1Child2" to="/home" onClick={(e) => {
                        const imgRef = ref(storage, `img${user.uid}`);
                        uploadBytes(imgRef, avatarFile).then(() => {
                            getDownloadURL(imgRef).then((avatarURL) => {
                                setAvatarURL(avatarURL);
                                updateProfile(user, {photoURL: avatarURL});                                
                                window.location.reload();
                            });
                        });
                    }}>Confirm</Link>
                </div>
            </div>
            <div id="editCon2">
            </div>
        </div>
    );
};

export default Edit;