import {HashRouter, Routes, Route} from 'react-router-dom';
import {useEffect, useState} from 'react';
import {onAuthStateChanged, signOut} from 'firebase/auth';
import {auth, storage} from './firebaseConfig.js';
import Login from './comp/Login.js';
import Home from './comp/Home.js';
import Side from './comp/Side.js';
import User from './comp/User.js';
import Edit from './comp/Edit.js';
import './style/App.css';

const App = () => {
  const [user, setUser] = useState(null);
  const [avatarURL, setAvatarURL] = useState(null);
  const [showSide, setShowSide] = useState("Null");

  useEffect(() => {
    onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });  
  });

  const logout = async () => {
    signOut(auth);
  };

  if(user != null){
    return (
      <HashRouter>
        <Routes>
          <Route path="/" element={<Home user={user} logout={logout} 
            setShowSide={setShowSide}/>}/>
          <Route path="/home" element={<Home user={user} logout={logout} 
            setShowSide={setShowSide}/>}/>
          <Route path="/user" element={<User logout={logout} user={user}/>}/>
          <Route path="/edit" element={<Edit user={user} avatarURL={avatarURL} 
          setAvatarURL={setAvatarURL}/>}/>
        </Routes>
        <Side user={user} showSide={showSide} setShowSide={setShowSide}/>
      </HashRouter>
    )
  }
  else
  {
    return <Login auth={auth}/>
  }
};

export default App;
