import {useState} from "react";
import {Link} from "react-router-dom";
import {createUserWithEmailAndPassword, 
    signInWithEmailAndPassword} from "firebase/auth";
import logo from "../style/images/logo.png";
import "../style/Login.css";

const Login = ({auth}) => {
    const [loginEmail, setLoginEmail] = useState(null);
    const [loginPass, setLoginPass] = useState(null);
    const [regName, setRegName] = useState(null);
    const [regEmail, setRegEmail] = useState(null);    
    const [regPass, setRegPass] = useState(null);
    const [showLogin, setShowLogin] = useState("2");
    const [showReg, setShowReg] = useState("Null");

    const register = async (e) => {
        e.preventDefault();
        try{
          await createUserWithEmailAndPassword(auth, regEmail, regPass);
        }
        catch(error){
          console.log(error.message);
        };
      };
    
      const login = async (e) => {
        e.preventDefault();
        try{
          await signInWithEmailAndPassword(auth, loginEmail, loginPass);
        }
        catch(error){
          console.log(error.message);
        };
      };

    return (
        <div id="loginCon">
            <div id="loginCon0"></div>
            <div id="loginCon1">
                <div id="loginCon1Child0">
                    <img className="loginCon1Child0" src={logo}/>
                </div>
                <div id="loginCon1Child1">
                    <button className="loginCon1Child1" onClick={() => {
                        setShowLogin("2");
                        setShowReg("Null");
                    }}>Login</button>
                    <button className="loginCon1Child1" onClick={() => {
                        setShowReg("3");
                        setShowLogin("Null");
                    }}>Register</button>
                </div>
                <form id={`loginCon1Child${showLogin}`}>
                    <input className="loginCon1Child2" placeholder="Email" type="email" onChange={(e) => setLoginEmail(e.target.value)}/>
                    <input className="loginCon1Child2" placeholder="Password" type="password" onChange={(e) => setLoginPass(e.target.value)}/>
                    <button className="loginCon1Child2" onClick={login}>Login</button>
                </form>
                <form id={`loginCon1Child${showReg}`}>
                    <input className="loginCon1Child3" placeholder="Name" onChange={(e) => setRegName(e.target.value)}></input>
                    <input className="loginCon1Child3" placeholder="Email" type="email" onChange={(e) => setRegEmail(e.target.value)}></input>
                    <input className="loginCon1Child3" placeholder="Password" type="password" onChange={(e) => setRegPass(e.target.value)}></input>
                    <button className="loginCon1Child3" to="" onClick={register}>Register</button>
                </form>
            </div>
            <div id="loginCon2"></div>
        </div>
    );
};

export default Login;